body {
  margin: 5% 0%;
  background-color: lightgrey; }

button {
  border-radius: 5px;
  background-color: red;
  border: groove red;
  padding: 2px 15px; }
  button:hover {
    background-color: blue;
    transition: background-color 1s; }

input {
  border: groove blue; }

.container {
  margin: auto;
  width: 50%;
  border: 7px groove lightblue;
  border-radius: 10px;
  padding: 10px;
  text-align: center;
  background: linear-gradient(lightblue, grey); }

.container a {
  border-radius: 5px;
  color: inherit;
  text-decoration: none;
  font-family: courier;
  font-style: italic;
  background-color: lightgreen;
  border: 2px groove green; }
  .container a:hover {
    background-color: blue;
    transition: 1s; }

.container .email {
  position: relative;
  left: 45%;
  bottom: 80px; }

.dropdown {
  position: relative;
  display: inline-block; }

.dropdown-content {
  display: none;
  position: absolute;
  z-index: 1; }

.dropdown:hover .dropdown-content {
  display: block;
  background: cornsilk;
  border: 1px solid black;
  width: 100%; }

.dropdown-content p:hover {
  background-color: red;
  transition: background-color 1s;
  border-radius: 0px; }

.textfield {
  background-color: tan;
  border: 2px solid black;
  padding: 15px;
  position: relative;
  top: 25px;
  margin: auto;
  width: 250px;
  float: left; }
  .textfield:nth-of-type(2) {
    float: right; }
